import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { environment } from 'environments/environment';
import { Observable, Subject, map } from 'rxjs';
import { IOrdenDeCompra } from '../models/orden-de-compra';
import { AuthService } from '@core/auth/auth.service';
import { Response } from '@shared/models/response-with-token';

@Injectable()
export class OrdenesDeCompraService {
    constructor(private http: HttpClient, private _authService: AuthService) { }
    /**
     * Funcion en el servicio
     */
    getOrdenesDeCompra(sort?: any): Observable<Response[]> {
        let f = '?';
        f += sort == 'desc' ? 'sort=-descripcion' : 'sort=descripcion';
        let subject = new Subject<any>();

        this.http.get<Response[]>(`${environment.administracion.view_clientes}${f}`)
            .subscribe((resp: any) => {
                this._authService.accessToken = resp.token,
                    subject.next({
                        'list': resp.result,
                        // 'totalPages': resp.headers.get('x-pagination-page-count'),
                        // 'totalCount': resp.headers.get('x-pagination-total-count')
                    })
            });
        return subject.asObservable();
    }

    /**
     * Funcion en el Servicio 
     * @param orden_de_compra 
     * @returns Crea un item nuevo
     */
    saveOrdenDeCompra(orden_de_compra: any): Observable<any> {
        return this.http.post<any>(`${environment.compras.compras_orden_compra}`, orden_de_compra);
    }

    /**
     * Funcion en el servicio
     * @param orden_de_compra
     * @returns Item actualizado actualizado
     */
    updateOrdenDeCompra(orden_de_compra: any, idordencompra: number): Observable<any> {
        return this.http.put<any>(`${environment.compras.compras_orden_compra}` + '/' + idordencompra, orden_de_compra);
    }

    /**
     * Funcion en el servicio
     * @param codigo 
     * @returns Elimina un item
     */
    deleteOrdenDeCompra(codigo: number): Observable<IOrdenDeCompra> {
        return this.http.delete<IOrdenDeCompra>(`${environment.compras.compras_orden_compra}` + '/' + codigo);
    }

    getProveedores(option?) {
        let f = '?sort=RazonSocial';
        f += !option ? '&filter[activo]=1' : '';
        let subject = new Subject<any>();
        this.http.get<Response[]>(`${environment.administracion.proveedores}${f}`)
            .subscribe((resp: any) => {
                // this._authService.accessToken = resp.token;
                subject.next({
                    list: resp
                })
            });
        return subject.asObservable();
    }

    getArticulos(idarticulo?: number, filterLike?: string) {
        let f = `?filter[activo]=1&pagination=1&per-page=10&page=1`;
        if (idarticulo) {
            f += `&filter[idarticulo]=${idarticulo}`;
        } else if (filterLike) {
            f += `&filter[or][0][codigo_interno][like]=${filterLike}&filter[or][1][descripcion][like]=${filterLike}`;
        }

        let subject = new Subject<any>();
        this.http.get<any>(`${environment.stock.articulos}${f}`).pipe(
            map((resp => resp))
        ).subscribe((resp: any) => {
            this._authService.accessToken = resp.token,
                subject.next({
                    list: resp.result
                })
        });
        return subject.asObservable()
    }

    getUnidadesDeMedidaArticulos(idarticulo) {
        let f = `?filter[idarticulo]=${idarticulo}`
        f += `&expand=um`
        let subject = new Subject<any>();
        this.http.get<Response[]>(`${environment.stock.articulo_unidad_medidas}${f}`)
            .subscribe((resp: any) => {
                // this._authService.accessToken = resp.token;
                subject.next({
                    list: resp.result
                })
            });
        return subject.asObservable()
    }

    getItems(idordencompra: number) {
        let f = `?filter[ordenCompra]=${idordencompra}`;
        let subject = new Subject<any>();
        this.http.get<Response[]>(`${environment.compras.compras_orden_compra_items}${f}`)
            .subscribe((resp: any) => {
                this._authService.accessToken = resp.token;
                subject.next({
                    list: resp.result
                })
            });
        return subject.asObservable();
    }
}