import { Component, EventEmitter, Input, OnDestroy, OnInit, Output, ViewChild } from '@angular/core';
import { IOrdenDeCompra } from '../models/orden-de-compra';
import { OrdenesDeCompraService } from '../service/ordenes-de-compra.service';
import { Subject, Subscription, debounceTime, distinctUntilChanged, firstValueFrom, takeUntil } from 'rxjs';
import { MatTableDataSource } from '@angular/material/table'
import { SaveComponent } from './save/save.component';
import { FuseConfirmationService } from '@fuse/services/confirmation';
import { AppAlertService } from '@shared/service/app/alert.service';
import { FormControl } from '@angular/forms';
import { TablaDinamicaComponent } from '@shared/components/tabla-dinamica/tabla-dinamica.component';
import { MatDialog } from '@angular/material/dialog';
import { environment } from 'environments/environment';
import { SearchService } from 'app/layout/common/search/search.service';
import { MatButton } from '@angular/material/button';
import { JedsMenuService } from '@shared/service/app/jeds-menu.service';
import { FilterMenuComponent } from './filter-menu/filter-menu.component';
import { IFilter } from '@shared/models/filter';
import { HeaderTextService } from '@shared/components/header-text/header-text.service';
import { Router } from '@angular/router';
import { EmpresaService } from '@shared/service/app/empresa.service';
@Component({
    selector: 'app-ordenes-de-compra',
    templateUrl: './ordenes-de-compra.component.html',
    styleUrls: ['./ordenes-de-compra.component.scss']
})
export class OrdenesDeCompraComponent implements OnInit, OnDestroy {
    ordenes_de_compra: IOrdenDeCompra[];
    public dataSource: MatTableDataSource<IOrdenDeCompra> = new MatTableDataSource();
    headers = ['N°', 'Fecha', 'Observaciones', 'Proveedor', 'Presupuesto', 'Pendiente de Entrega', 'Acciones'];
    column_params = ['numero', 'fecha', 'observaciones', 'proveedor_descripcion', 'pedido', 'estado', 'acciones'];
    funcionesObjeto = null;
    filtroBusqueda: string = '';
    filtersLike = ['observaciones', 'proveedor_descripcion', 'numero'];
    filter: IFilter = {
        proveedor: '',
        pendiente_entrega: -1,
    };
    @Input() extraParams = '';
    @Output() parametrosActualizados = new EventEmitter<any>();
    env = environment.compras.view_orden_compras + '?sort=-codigo';
    search: FormControl = new FormControl('');
    sortValue: string;
    FilterEventSub$: Subscription;
    SearchTextSub$: Subscription;
    color_primario: string;
    color_secundario: string;
    default_color: string = 'border-primary text-primary';
    private _unsubscribeAll: Subject<any> = new Subject<any>();

    constructor(
        private _ordenesDeCompraService: OrdenesDeCompraService,
        private _empresaService: EmpresaService,
        private confirm: FuseConfirmationService,
        private alert: AppAlertService,
        private dialogo: MatDialog,
        private _searchService: SearchService,
        private _menuService: JedsMenuService,
        private router: Router,
        public _headerTextService: HeaderTextService,

    ) {
        this._headerTextService.setTitulo(this.router);

        /**
         * Aca se declaran los botones que iran en la grilla sobre el apartado de acciones.
         */
        this.funcionesObjeto = [
            {
                nombre_boton: "Editar",
                functionName: 'editar',
                iconoAccion: (orden_compra: IOrdenDeCompra) => orden_compra.editable === 1 ? 'jedstion:editar' : 'jedstion:editar_disabled',
                iconoAccionado: (orden_compra: IOrdenDeCompra) => '',
                iconoDeshabilitado: (orden_compra: IOrdenDeCompra) => '',
            },
            {
                nombre_boton: "Eliminar",
                functionName: 'eliminar',
                iconoAccion: (orden_compra: IOrdenDeCompra) => 'jedstion:eliminar',
                iconoAccionado: (orden_compra: IOrdenDeCompra) => '',
                iconoDeshabilitado: (orden_compra: IOrdenDeCompra) => '',
            },
            {
                icono: 'impresora',
                nombre_boton: "Reporte de OC",
                functionName: 'reporte_orden_de_compra',
                iconoAccion: (orden_compra: IOrdenDeCompra) => 'jedstion:imprimir_disabled',
                iconoAccionado: (orden_compra: IOrdenDeCompra) => '',
                iconoDeshabilitado: (orden_compra: IOrdenDeCompra) => '',
            }
        ];
    }

    @ViewChild(TablaDinamicaComponent) tabla: TablaDinamicaComponent;
    ngOnInit(): void {
        // Subscribe to empresa data
        this._empresaService.empresa$
            .pipe(takeUntil(this._unsubscribeAll))
            .subscribe(empresa => {
                this.color_primario = empresa.color_primario;
                this.color_secundario = empresa.color_secundario;
            });

        // Load empresa data
        this._empresaService.getEmpresa();
        this.SearchTextSub$ = this._searchService.getSearchText().pipe( //Se obtienen los valores que coinciden con lo ingresado en la barra de busqueda
            debounceTime(750),
            distinctUntilChanged()
        )
            .subscribe(value => {
                this.filtroBusqueda = value;
                this.tabla.filters(this.filtroBusqueda);
            });

        this.FilterEventSub$ = this._searchService.getFilterEvent().subscribe(event => {
            this.onFilterMenu(this._searchService.getFilterOrigin())
        });
    }

    onFilterMenu(origin: MatButton) {
        this._menuService
            .open(FilterMenuComponent, origin._elementRef, {
                filter: this.filter,
            })
            .afterClosed()
            .subscribe({
                next: filter => {
                    if (filter) {
                        this.filter = filter;
                        this.extraParams = '';
                        // Se va a verificar si se ha seleccionado "Todos" como proveedor
                        if (this.filter?.proveedor !== '') {
                            // Se aplicara el filtro de proveedor seleccionado
                            this.extraParams += "filter[proveedor_descripcion][like]=" + this.filter.proveedor + '&';
                        }
                        this.extraParams += this.filter.filtroFechaInicio != null ? "filter[fecha][gte]=" + this.filter.filtroFechaInicio + '&' : '';
                        this.extraParams += this.filter.filtroFechaFin != null ? "filter[fecha][lte]=" + this.filter.filtroFechaFin + '&' : '';
                        this.extraParams += this.filter.pendiente_entrega != -1 ? "filter[estado]=" + this.filter.pendiente_entrega + '&' : '';

                        this.tabla.filters(this.filtroBusqueda);
                    }
                },
            });
    }

    /**
 * Esta es la funcion que se encarga de la columna acciones dentro de la grilla dinamica
 * @param event 
 */
    generarAcciones(event: { name: string, params: any }) {
        switch (event.name) {
            case 'editar':
                if (event.params.data.editable === 1) {
                    this.updateOrdenDeCompra(event);
                } else {
                    this.alert.warning('No puede editar esta Orden de Compra porque tiene Remitos asociados');
                }
                break;
            case 'eliminar':
                this.deleteOrdenDeCompra(event);
                break;
            case 'reporte_orden_de_compra':
                this.alert.info('Esta función estará disponible proximamente');
                // this.reporteOrdenDeCompra(event);
                break;
        }
    }

    /**
     * Esta funcion guarda un item
     */
    saveOrdenDeCompra() {
        this.dialogo
            .open(SaveComponent, { panelClass: 'ayuda-dialog' })
            .componentInstance.creadoExitosamente
            .subscribe({
                next: () => {
                    this.alert.success("Orden de compra creada con éxito.");
                    this.tabla.filters(this.filtroBusqueda);
                },
            });
    }

    /**
     * Esta funcion modifica un item
     * @param event 
     */
    updateOrdenDeCompra(event) {
        this.dialogo
            .open(SaveComponent, { data: event.params.data, panelClass: 'my-dialog', autoFocus: false })
            .componentInstance.editadoExitosamente
            .subscribe({
                next: () => {
                    this.alert.success("Orden de compra editada con éxito.");
                    this.tabla.filters(this.filtroBusqueda);
                },
            });
    }

    /**
     * Esta funcion elimina un item
     * @param event 
     */
    deleteOrdenDeCompra(event) {
        this.confirm.open({
            title: "Eliminar orden de compra N°" + event.params.data.codigo,
            message: `¿Desea eliminar la orden de compra: N° ${event.params.data.codigo} del proveedor ${event.params.data.proveedor_descripcion} ?`,
            dismissible: false,
            actions: {
                confirm: {
                    show: true,
                    color: 'warn',
                    label: 'Si',
                },
                cancel: {
                    show: true,
                    label: 'No',
                },
            },
        })
            .afterClosed()
            .subscribe(res => {
                if (res === 'confirmed') {
                    this._ordenesDeCompraService.deleteOrdenDeCompra(event.params.data.codigo).subscribe({
                        next: (res) => {
                            // Manejar la respuesta exitosa
                            this.alert.success("Orden de compra eliminada con éxito.");
                            if (res) {
                                this.tabla.renderTabla()
                            }
                        },
                        error: (error) => {
                            // Manejar el error
                            console.error('Error en el servicio:', error);
                            this.alert.warning("Esta orden de compra no puede eliminarse. Tiene elementos asociados.");
                        }
                    });
                }
            });
    }

    ngOnDestroy(): void {
        this._searchService.unsubscribe()
    }
}